.header {
    color: $siberian-blue;
    font-size: 18px;
    line-height: 28px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 20px;
    padding: 26px 0;

    @media screen and (max-width: 992px){
        font-size: 16px;
        line-height: 24px;
    }
    @media screen and (max-width: 768px){
        font-size: 14px;
        line-height: 20px;
    }

    @media screen and (max-width: 768px){
        font-size: 12px;
        line-height: 16px;
    }
}

.nav {
    display: flex;
    align-items: center;
    .menu {
        display: flex;
        li {
            margin: 0 20px;
            cursor: pointer;

            a {
                display: block;
                height: 100%;
            }

            &::after {
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                background-color: $siberian-blue;
                transform: translateY(26px) scale(0);
                transition: all 0.3s;
            }
            
            &.active {
                &::after{
                    transform: translateY(26px) scale(1);
                }
            }
        }
    }
}

.username {
    margin-right: 15px;
    color: $black;
}

.logout {
    color: $siberian-blue;
    cursor: pointer;

    @media screen and (max-width: 576px){
        margin-top: 0.5rem;
    }
}

.right {
    display: flex;

    @media screen and (max-width: 576px) {
        flex-direction: column;
    }
}